import React from 'react'
import './styles.css'
import TextStats from '../TextStats'
import Interval from '../Interval'
import httpError from '../httpError'

import Progress from 'react-progress'
import axios from 'axios'
import Cookies from 'js-cookie'

class Leads extends React.Component {
  constructor(props) {
    super(props);
    this.state = { missed:0, halfMissed:0, overMissed:0, progress:0, interval: {} }

    this.onInterval = this.onInterval.bind(this)    
  }

  componentDidMount() {
    const fromLocalStorage = localStorage.getItem('leads')
    if (fromLocalStorage) this.setState(JSON.parse(fromLocalStorage))
    this.setState({ msid: Cookies.get('msid') }, () => this.fetchInfo())
  }

  fetchInfo() {
    this.setState({ progress: 7 })
    const delay = Math.floor(Math.random() * (100 - 10 + 1)) + 10
    const progress = Math.floor(Math.random() * (25 - 10 + 1)) + 10
    setTimeout(() => this.setState({ progress  }), delay)

    const { msid } = this.state
    let url = `http://papi.mindsales-crm.com/stats/leads/fucked?token=${msid}`

    axios.get(url)
      .then(({ data }) => {
        this.setState({ progress: 100 })
        const { missed, halfMissed, overMissed } = data
        this.setState({ missed, halfMissed, overMissed })
        localStorage.setItem('leads', JSON.stringify({ missed, halfMissed, overMissed }))
      })
      .catch(httpError);    
  }    

  onInterval(interval) {
    this.setState({ interval }, () => this.fetchInfo());    
  }  

  drawManagers() {
    const { managers } = this.state
    return managers.length > 0?
      managers.map(({ manager, count}, index) => <TextStats key={index} title={manager} value={count} />)
      :  
      (<div>Данных нет</div>)
  }
  
  render(){
    return (
      <div className="bContent">
        <Progress style={{boxShadow:'none'}} percent={this.state.progress} color="#D29FCD" height="12" />
        <h1>Пропущенные лиды</h1>
        <div className="innerContent">
            <div class="leadsOfPain">
              <div class="leadsOfPain__all">{this.state.missed}</div>
              <div class="leadsOfPain__details ">
                <TextStats title="Никто не ответил и не перезвонил" value={this.state.overMissed} size="40"/>
                <TextStats title="Менеджеры не дозвонились" value={this.state.halfMissed} />
              </div>
            </div>          
        </div>
      </div>   
    )
  }
}  

export default Leads



